
import { debounce, throttle } from "lodash-es";

export let inView = {
    container: new Map(),
    options: null,
    handler: null,
    debouncer: null,
    init: function (opt) {

        if (opt === undefined || opt === null) {
            throw new Error("You have to provide options for InView to work!");
        }

        this.debouncer = throttle(this.observe.bind(this), 150, {
            'leading': true,
            'trailing': true
        });

        this.options = Object.assign({ selector: "[data-invw]", onlyY: false }, opt);
        this.handler = this.options ? this.options.exec : () => { };

        let elems = document.querySelectorAll(this.options.selector);

        elems.forEach((el, id, parent) => {
            !this.container.has(el) && this.container.set(el, null);
        });

        this.observe(); // check during init what elements are already in view
        this.startObserve();
    },
    addElement: function (el) {
        !this.container.has(el) && this.container.set(el, null);
    },
    removeElement: function (el) {

        if (this.container.has(el)) {
            // console.log("----------------------REMOVE-----------------------")
            // console.log(el);
            this.container.delete(el);

        }

    },
    clear: function () { this.container.clear(); },
    /**
     * 
     * @param {HTMLElement} elem 
     */
    isInView: function (elem) {
        var distance = elem.getBoundingClientRect();

        if (this.options.onlyY) {
            let elHeight = distance.bottom - distance.top;
            let vpHeight = (window.innerHeight || document.documentElement.clientHeight);

            // if (elHeight >= vpHeight) {
            if (elHeight >= (vpHeight * 0.5)) {
                console.log("-----------------  el is higher than viewport  -------------------------------");
                console.log(elem.classList);
                let inview = (distance.top >= 0 && (distance.bottom * 0.5) <= vpHeight);
                console.log("is inview: " + inview);
                return inview;
            }

            let isVerticallyInViewPort = (
                distance.top >= 0 &&
                distance.bottom <= vpHeight);

            return isVerticallyInViewPort;
        }
        return (
            distance.top >= 0 &&
            distance.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
            distance.left >= 0 &&
            distance.right <= (window.innerWidth || document.documentElement.clientWidth)
        );
    },
    observe: function () {
        console.log("calling observe");
        let self = this;
        this.container.forEach((val1, el, set) => {

            let lastVieportState = set.get(el);
            // console.log("State:" + elState);
            let currentviewportstate = self.isInView(el);

            set.set(el, currentviewportstate);
            self.handler.call(el, currentviewportstate, lastVieportState, self.removeElement.bind(self));
            // }
        });
    },
    startObserve: function () {
        window.addEventListener("scroll", this.debouncer);
    },
    stopObserve: function () {
        window.removeEventListener("scroll", this.debouncer);
    },
    destroy: function () {
        this.stopObserve();
        this.container.clear();
    }
}

